import React, {PropTypes} from 'react';
import KlassName from 'teapan/klassName';
import Icon from '../icon/Icon';
import Button from '../button/Button';

const noop = () => {};

class TabControl extends React.Component {
	constructor(props) {
		super(props);
	}

	render() {
		const props = this.props;
		const {
			onNextClick,
			onPrevClick,
			className,
			...others
		} = props;

		const klassName = KlassName('tab-control', className);

		return (
			<div
				{...others}
				className={klassName}
			>
				<Button size="small" kind="link" shape="circle" skin="error" onClick={onPrevClick} role="tabcontrol"><Icon name="chevronLeft" /></Button>
				<Button size="small" kind="link" shape="circle" skin="error" onClick={onNextClick} role="tabcontrol"><Icon name="chevronRight" /></Button>
			</div>
		);
	}
};

TabControl.propTypes = {
	className: PropTypes.string,
	onPrevClick: PropTypes.func,
	onNextClick: PropTypes.func,
};

TabControl.defaultProps = {
	onPrevClick: noop,
	onNextClick: noop
};

export default TabControl;